import React, { Component } from 'react'
import Button from '@atlaskit/button';

import "./Option.scss";

interface IOptionProps {
    option: string;
    price: string;
    desc: string[];
    select: (val) => void; 
    submit: () => void;
    submitting?: boolean;
}
export default class Option extends Component<IOptionProps, {}> {
  render() {
      const { option, price, desc, select, submit, submitting } = this.props;

      const listItems = desc.map((d) => <li style={{fontStyle:"italic", marginBottom: 5}}>{d}</li>);

    return (
      <div className="option">
        <div className="option-header">
            <h3>{option}</h3>
        </div>
        <div className="option-body">
            <p>{price}</p>
            <ul>
                {listItems}
            </ul>
            <Button onClick={() => {select(option);}}>Select</Button>
        </div>
      </div>
    )
  }
}
