import React, { Component } from 'react'
import Button from '@atlaskit/button';
import FieldText from '@atlaskit/field-text';
import DropdownMenu, { DropdownItemGroup, DropdownItem, DropdownMenuStateless } from '@atlaskit/dropdown-menu';

interface IStep1Props {
  selectStuff: (step1name, step1type, step1email, step1founded, step1cvr) => void;
  nextStep?: () => void;
}
export default class Step1 extends Component<IStep1Props,{}> {

  state = {
    name: "",
    type: "",
    email: "",
    founded: "",
    cvr: "",
  }

  render() {
      const { selectStuff, nextStep } = this.props;
    return (
      <div className="step">
        <h2>
          Welcome
        </h2>
        <h3>
          Please register your company 
        </h3>
          <div style={{marginBottom: 20}}>
          <FieldText
            onChange={(val) => this.setState({name: val.currentTarget.value})}
            label="Company name"
            required
            name="example-text"
          />
          </div>
          <div style={{marginBottom: 10}}>
            <DropdownMenu
              trigger="Type"
              triggerType="button"
              shouldFlip={false}
              onOpenChange={e => this.setState({type: e.event.target.textContent})}
            >
              <DropdownItemGroup>
                <DropdownItem id="as">A/S</DropdownItem>
                <DropdownItem>ApS</DropdownItem>
                <DropdownItem>IVS</DropdownItem>
                <DropdownItem>I/S</DropdownItem>
                <DropdownItem>Enkeltmandsvirksomhed</DropdownItem>
              </DropdownItemGroup>
            </DropdownMenu>
            </div>
            <div style={{marginBottom: 20}}>
            <FieldText
            onChange={(val) => this.setState({cvr: val.currentTarget.value})}
            label="CVR"
            required
            name="example-text"
          />
          </div>
          <div style={{marginBottom: 10}}>
          <FieldText
            onChange={(val) => this.setState({founded: val.currentTarget.value})}
            label="Year founded"
            required
            name="example-text"
          />
            </div>
            <div style={{marginBottom: 20}}>
            <FieldText
            onChange={(val) => this.setState({email: val.currentTarget.value})}
            label="Company email"
            required
            name="example-text"
          />
            </div>
            <div style={{display:"flex",justifyContent:"center",width:"100%"}}>
        <Button onClick={() => {if(nextStep) {nextStep(); selectStuff(this.state.name,this.state.type,this.state.email,this.state.founded,this.state.cvr)}}}>Next</Button>
        </div>
      </div>
    )
  }
}