import React, { Component } from 'react'
import StepWizard from 'react-step-wizard';
import "./SignupSection.scss"
import Nav from '../../nav';
import Step1 from './step/Step1';
import Step2 from './step/Step2';
import Step3 from './step/Step3';


export default class SignupSection extends Component {

  state = {
    done: false,
    option: "",
    name: "",
    type: "",
    email: "",
    founded: "",
    cvr: "",
    location: "",
    country: "",
    industry: "",
    prodtype: "",
    stage: "",
    amount: "",
    currency: "",
    valuation: "",
    submitting: false,
  }

  render() {
    return (
      <div className="section">
        {!this.state.done ? <StepWizard
                                selectStuff          
                                //onStepChange={this.onStepChange}
                                isHashEnabled
                                nav={<Nav />}
        >
          <Step1 selectStuff={this.onStep1Change}></Step1>
          <Step2 selectStuff={this.onStep2Change}></Step2>
          <Step3 selectStuff={this.onStep3Change} submit={this.submit} submitting={this.state.submitting}></Step3>
        </StepWizard> : <p>Thanks for submitting</p>}
      </div>
    )
  }

  onStep1Change = (step1name, step1type, step1email, step1founded, step1cvr) => {
    this.setState({name: step1name})
    this.setState({type: step1type})
    this.setState({email: step1email})
    this.setState({founded: step1founded})
    this.setState({cvr: step1cvr})
  }

  onStep2Change = (step2location, step2country, step2industry, step2prodtype, step2stage, step2amount, step2currency, step2valuation) => {
    this.setState({location: step2location})
    this.setState({country: step2country})
    this.setState({industry: step2industry})
    this.setState({prodtype: step2prodtype})
    this.setState({stage: step2stage})
    this.setState({amount: step2amount})
    this.setState({currency: step2currency})
    this.setState({valuation: step2valuation})
  }

  onStep3Change = (val) => {
    this.setState({option: val}, () => {
      console.log(this.state.option)
      this.submit();
    })
  }

  submit = () => {
    this.setState({submitting: true})
    
    this.setState({done: true})
    fetch(`http://3.122.134.190:8000/signup?name=${this.state.name}&companytype=${this.state.type}&email=${this.state.email}&founded=${this.state.founded}&cvr=${this.state.cvr}&location=${this.state.location}&country=${this.state.country}&industry=${this.state.industry}option&prodtype=${this.state.prodtype}&stage=${this.state.stage}&amount=${this.state.amount}&currency=${this.state.currency}&valuation=${this.state.valuation}&plan=${this.state.option}`).catch(e => console.log(e))
    console.log("submitting")
  }
}
